export default {
  name: 'messageInfo',
  data () {
    return {
      info: null
    }
  },
  methods: {
    getData () {
      this.$http(this.$urlPath.messageDetails, {
        id: this.$routeParams.mid
      }, '', (data) => {
        this.info = data.data
      }, (errorCode, error) => {
        this.$toast(error)
      })
    }
  },
  onLoad () {
    this.getData()
  }
}
